.header{
    background-color: #282c34;
    width: 250px;
    height: 25px;
    display: flex;
    justify-content: space-between;
    transition-duration: 500ms;
    transform: translate(-225px);
}
.header:hover{
    transition-duration: 500ms;
    transform: translate(0px);
}
.link{
    color: #dadee7;
    text-decoration: none;
}
.link:hover{
    color: #61dafb;
    text-decoration: none;
}
.active{
    color: darkred;
}
.block{
    width: 25px;
    height: 25px;
    background-color: blueviolet;
}