.label{
    display: flex;
    align-items: center;
    cursor: pointer;
}

.checkbox {
    appearance: none;
    width: 20px;
    height: 20px;
    cursor: pointer;
    border: 1px solid #abb3c3;
    background-color: #fff;
}

.checkbox:hover {
    background-color: #fff;
    box-shadow: 0px 0px 1px 1px rgba(0, 0, 0, 0.7);
}
.checkbox:checked {
    appearance: none;
    background-image: url("checked3.png");
}

