.main {
    height: 125px;
    width: 400px;
    display: flex;
    align-items: flex-end;
    background-color: greenyellow;
    margin: 10px;
    padding: 10px;
}

.avatar {
    width: 80px;
    height: 80px;
    border: 1px solid;
    border-radius: 50px;
}

.content {
    height: 100px;
    width: 250px;
    padding: 5px;
    border-radius: 5px 5px 5px 0px;
    background-color: #282c34;
}
.angle {
    width: 15px;
    height: 15px;
    background: radial-gradient(circle at left top, rgba(0,0,0,0) 15px, #282c34 0);
}
.name {
    font-size: 25px;
    color: #61dafb;
}
.message {
    font-size: 20px;
    color: white;
    word-wrap: break-word;
}
.time{
    display: flex;
    justify-content: flex-end;
    font-size: 20px;
    color: firebrick;
}
